@import "../lib/slick/slick.scss";
@import "../lib/slick/slick-theme.scss";

* {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Chrome/Safari/Opera */
     -khtml-user-select: none; /* Konqueror */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently not supported by any browser */
}

* { box-sizing: border-box; }

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}

body { background-color: #fff; }

.app-shell {
  width: 403px;
  height: 803px;
  background: url(../images/nexus-5-shell.jpg) top left no-repeat;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.app {
  background-color: #fff;
  height: 594px;
  width: 362px;
  margin: 67px auto;
  overflow-y: hidden;
  color: #111;
  position: relative;
}

.tela {
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  text-align: center;
}

.intro-comprador {
  background: url(../images/intro-bg.jpg) top left no-repeat;
}

.intro-vendedor {
  background: url(../images/intro-bg.jpg) top left no-repeat;
}

.infos {
  img { margin: 0 auto; }
  .text-under-icon {
    font-weight: bold;
    margin: 20px 0;
  }
}

.icon-cesta-casa {
  margin-top: 45px !important;
  margin-bottom: 50px !important;
}

.icon-melancia-limao {
  margin-top: 45px !important;
  margin-bottom: 50px !important;
}

.btn-pular {
  width: 80%;
}

.slick-slide {
  outline: none
}

.login {
  background: url(../images/intro-bg.jpg) top left no-repeat;
}

.logo-pedeincasa { margin: 20px auto; }

.btn-facebook {
  width: 80%;
}

.full-width {
  width: 100%;
}

.container {
  width: 80%;
  margin: 0 auto;
}

.intro-footer {
  padding-top: 20px;
}

.login {
  a {
    font-weight: 700;
    color: #111;
    text-decoration: underline;

    &:hover,
    &:focus {
      color: #0d4b8a;
    }
  }

  .faca-cadastro {
    font-size: 1.1em;
  }

  .termos-uso {
    font-size: 0.85em;
  }
}

.ui.segment {
  border-radius: 0;

  .ui.menu {
    border-radius: 0;
  }
}

.tela.cadastro {
  color: #fff;
  padding: 0;
  text-align: left;

  .step-bar {
    float: right;
  }

  .user-avatar {
    max-width: 30%;
    margin: 0 auto;
  }

  .gender-select {text-align: center;}

  .field {
    width: 100%;
  }

  //button { width: 100%; }

  .logo-topo { padding: 20px 0;}

  .btn-confirmar { margin-top: 10px;}
}

.passo {
  padding: 0 1% 1%;
}

.btn-back,
.btn-home {
  text-decoration: none;
  color: #fff;
  font-size: 1.2em;
}

.form label {
  text-align: left;
}

.info-cartao .row {
  padding: 5px !important;
}

.tela.home {
  padding: 0;
  text-align: left;
  background: url(../images/map-bg.jpg) top left no-repeat;

  .sw-menu {
    color: #fff;
    font-size: 20px;
    line-height: 37px;
  }

  .search-bar {
    padding: 5px 0;
  }

  .home-comprador-1 {
    height: 530px;
    position: relative;

    .vendedores-perto {
      position: fixed;
      bottom: 35px;
      width: 325px;
    }
  }

  .barra-vendedores-perto {
    display: none;
    margin: 0;
  }
  .barra-pagamento {
    display: none;
    margin-top: 0;
  }

  .faca-busca {
    position: absolute;
    width: 362px;
    left: 0;
    bottom: 0px;
    padding-bottom: 20px;
    z-index: 1;
  }

  .slick-track {
    height: 546px;
  }

  .message { margin-top: 10px;}

  .topo-detalhes { margin-top: 10px;}

  .raised.segment {
    h1, h2, h3, h4, h5, h6 {
      margin: 10px 0;
    }
  }
}

.tela.home-vendedor {
  background: url(../images/map-vendedor-bg.jpg);

  .home-vendedor-1 {
    position: relative;
    height: 528px;
    &.passo { padding: 0;}

    .segment {
      width: 362px;
      margin: 0;
    }
    .resultados {
      position: absolute;
      width: 362px;
      left: 0;
      bottom: 0;
    }

    .blue.inverted.segment .column {
      padding: 5px;

      .header {
        text-align: center;
        font-size: 2em;
      }
      .content {
        text-align: center;
        padding: 10px 5px;
      }
    }
  }

  .barra-novo-chamado { display: none; margin: 0}

  .barra-enviar-cobranca {display: none; margin: 0;}

  .barra-vendas { display: none; margin: 0;}

  .custom-height {
    height: 545px;
  }

  .cobranca-1 { height: 547px; }
}
